<template>
  <div v-if="isShowPagination" class="pagination-container">
    <div class="pagination-paginate">
      <div v-if="renderPage.length > 5">
        <ul class="pagiantion-paginate-container">
          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--left"
            @click="changePage(currentPage - 1)"
            :class="{
              'no-cursor': currentPage === 1,
            }"
          >
            <i class="icon icon--arrow-left"></i>
          </li>

          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--index"
            v-bind:class="{
              'pagiantion-paginate__item--index--actived': currentPage === 1,
              'no-cursor': currentPage === 1,
            }"
            @click="currentPage !== 1 && changePage(1)"
          >
            <span>1</span>
          </li>

          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--three-dot no-cursor"
            v-if="
              currentPage >= renderPage.length - 2 ||
                (renderPage.length > 5 && currentPage >= 3)
            "
          >
            <i class="icon icon--three-dot-horizontal"></i>
          </li>

          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--index"
            v-bind:class="{
              'pagiantion-paginate__item--index--actived': true,
              'no-cursor': true,
            }"
            v-if="currentPage > 1 && renderPage.length - 1 > currentPage"
          >
            <span>{{ currentPage }}</span>
          </li>

          <li
            v-if="currentPage < renderPage.length"
            class="pagiantion-paginate__item pagiantion-paginate__item--index"
            v-bind:class="{
              'pagiantion-paginate__item--index--actived':
                currentPage === renderPage.length - 1,
            }"
            @click="changePage(getCurrentPage(currentPage))"
          >
            <span>{{ getCurrentPage(currentPage) }}</span>
          </li>

          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--three-dot no-cursor"
            v-if="currentPage < renderPage.length - 2"
          >
            <i class="icon icon--three-dot-horizontal"></i>
          </li>

          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--index"
            v-bind:class="{
              'pagiantion-paginate__item--index--actived':
                currentPage >= renderPage.length,
              'no-cursor': currentPage === renderPage.length,
            }"
            @click="
              currentPage !== renderPage.length && changePage(renderPage.length)
            "
          >
            <span>{{ renderPage.length }}</span>
          </li>

          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--right"
            :class="{
              'no-cursor': currentPage >= renderPage.length,
            }"
            @click="changePage(currentPage + 1)"
          >
            <i class="icon icon--arrow-right"></i>
          </li>
        </ul>
      </div>

      <div v-else>
        <ul class="pagiantion-paginate-container">
          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--left"
            @click="changePage(currentPage - 1)"
            :class="{
              'no-cursor': currentPage === 1,
            }"
          >
            <i class="icon icon--arrow-left"></i>
          </li>
          <li
            v-for="(page, index) in renderPage"
            :key="index"
            class="pagiantion-paginate__item pagiantion-paginate__item--index"
            v-bind:class="{
              'pagiantion-paginate__item--index--actived':
                currentPage === page ||
                (currentPage > page && page === renderPage.length),
            }"
            @click="changePage(page)"
          >
            <span>{{ page }}</span>
          </li>
          <li
            class="pagiantion-paginate__item pagiantion-paginate__item--right"
            :class="{
              'no-cursor': currentPage >= renderPage.length,
            }"
            @click="changePage(currentPage + 1)"
          >
            <i class="icon icon--arrow-right"></i>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import get from "@/util/get";

export default {
  name: "Pagination",
  data() {
    return {
      renderPage: [],
      totalPage: 1, // default
    };
  },
  props: {
    totalItems: {
      type: Number,
      default: 0,
    },
    showEntries: {
      type: Number,
      default: 10,
    },
    showPagination: Function,
  },
  computed: {
    isShowPagination() {
      return this.totalItems > 0;
    },
    currentPage() {
      return Number(get(this.$route, "query.page", 1));
    },
  },
  methods: {
    pageNumber(totalItems) {
      const pageNumber = (totalItems / this.showEntries).toFixed(0);
      this.totalPage = pageNumber == 0 ? 1 : pageNumber;
      this.showRenderPage();
    },
    showRenderPage() {
      let renderPage = [];
      for (let i = 1; i <= this.totalPage; i++) {
        renderPage.push(i);
      }
      this.renderPage = renderPage;
    },
    changePage(page) {
      const { showPagination } = this;
      showPagination(page, this.showEntries);
      this.$router.push(`?page=${page}`);
    },
    getCurrentPage(page) {
      const { currentPage, renderPage } = this;
      return (
        (currentPage === renderPage.length && page - 1) ||
        (currentPage === renderPage.length - 1 && page) ||
        page + 1
      );
    },
  },
  mounted() {
    const { totalItems, pageNumber } = this;
    pageNumber(totalItems);
  },
};
</script>

<style lang="scss" scoped>
.no-cursor {
  pointer-events: none;
}
.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.pagination-entries {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .form-control {
    display: inline-block;
    width: fit-content;
    padding: 6px 10px;

    &:focus {
      border-color: #ccc;
      box-shadow: unset;
    }
  }
}

.pagiantion-paginate-container {
  display: flex;
}
.pagiantion-paginate__item {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  margin: 0;
  padding: 0px 12px;
  line-height: 30px;

  &.pagiantion-paginate__item--three-dot {
    color: #4ea9df;
  }

  &.pagiantion-paginate__item--index {
    color: #dadada;
    font-weight: bold;
    font-size: 21px;
  }

  &.pagiantion-paginate__item--left {
    padding-left: 0;
  }
  &.pagiantion-paginate__item--right {
    padding-right: 0;
  }
}
.pagiantion-paginate__item--index--actived {
  color: #5450f7 !important;
}

@media screen and (min-width: 768px) {
  .pagiantion-paginate__item--index:hover {
    color: #5450f7;
  }
}
</style>
