<template>
  <!-- TODO: Color styling og SVG's, set color on hover and active -->
  <div class="account-credit__container">
    <h3>{{ $t("myCredit") }}</h3>
    <div class="account-credit__body">
      <div v-if="errorMsg" class="alert alert-danger" v-html="errorMsg" />
      <div class="account-credit__title">
        <label>{{ $t("currentCreditBalance") }}&nbsp;</label>
        <strong>{{ creditInfo.credit }} {{ $t("credit") }}</strong
        >&nbsp;
        <span>({{ creditInfo.credit_val | formatISK }})</span>
      </div>

      <div class="account-credit__body-container">
        <h3 class="account-credit__label">{{ $t("rechargeCode") }}</h3>
        <div class="account-credit__input">
          <div>
            <strong>{{ $t("tutorialText") }}</strong>
          </div>
          <div>
            <input
              type="text"
              class="form-control input"
              v-model="rechargeCode"
            />
          </div>
          <button class="btn-submit btn btn-primary btn-lg" @click="submitCode">
            <span>{{ $t("submitCode") }}</span>
            <span class="btn-icon">
              <i class="fa fa-angle-right"></i>
            </span>
          </button>
        </div>
      </div>

      <div>
        <h3 class="account-credit__label">{{ $t("recentAction") }}</h3>
        <table
          cellspacing="0"
          class="account-credit__table table"
          id="credit-table"
        >
          <thead>
            <tr>
              <th class="account-credit__action">{{ $t("action") }}</th>
              <th class="account-credit__change">{{ $t("change") }}</th>
              <th class="account-credit__balance">{{ $t("creditBalance") }}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="!isReady">
              <td colspan="3" class="account-credit__table-no-action">
                <LoadingWrapper
                  :loadingKeys="[fetchingCredit]"
                ></LoadingWrapper>
              </td>
            </tr>
            <tr v-else-if="!isHaveCredit">
              <td colspan="3" class="account-credit__table-no-action">
                <p>No actions yet.</p>
              </td>
            </tr>

            <div
              v-else
              v-for="credit in creditList"
              :key="credit.log_id"
              class="account-credit__item-container"
            >
              <tr class="account-credit__item-notes">
                <td colspan="3">
                  {{ credit.action_type }}&#8758;
                  <span
                    class="account-credit__item-comment"
                    v-html="credit.comment"
                  />
                </td>
              </tr>
              <tr class="account-credit__item-content">
                <td class="account-credit__action">
                  {{ credit.action_date | formatDateTime }}
                </td>
                <td class="account-credit__change">
                  {{ credit.value_change }}
                </td>
                <td class="account-credit__balance">
                  {{ parseInt(credit.value) | formatISK }}
                </td>
              </tr>
            </div>
          </tbody>
        </table>

        <div class="account-credit__pagination" v-if="isHaveCredit">
          <Pagination
            v-bind="paginationProps"
            :showPagination="getCreditList"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "myCredit": "Inneign",
    "currentCreditBalance": "Núverandi inneign þín: ",
    "rechargeCode": "Áfyllingarkóði",
