  },
  "en": {
    "myCredit": "Credits",
    "currentCreditBalance": "Your current credit balance: ",
    "rechargeCode": "Recharge code",
    "tutorialText": "Enter a recharge code to refill your balance. You can use this credit at any time during the checkout process.",
    "submitCode": "Continue",
    "recentAction": "Recent Actions",
    "creditBalance": "Balance",
    "change": "Change",
    "modifiedOn": "Modified On",
    "action": "Action",
    "comment": "Comment",
    "credit": "credits"
  }
}
