<script>
import { mapActions, mapGetters } from "vuex";

import { getCustomerCreditList, refillRechargeCode } from "@/services/customer";

import LoadingWrapper from "@/components/LoadingWrapper";
import Pagination from "@/components/Pagination";

import get from "@/util/get";

import { LOADING_KEY_FETCHING_ACCOUNT_CREDIT } from "@/store/loadingModule";

export default {
  name: "ViewMyAccountCredit",
  components: {
    LoadingWrapper,
    Pagination,
  },
  data() {
    return {
      errorMsg: "",
      rechargeCode: "",
      customerCredit: undefined,
      fetchingCredit: LOADING_KEY_FETCHING_ACCOUNT_CREDIT,
    };
  },
  computed: {
    ...mapGetters(["customerInfo"]),
    creditInfo() {
      return get(this.customerInfo, "customer_credit", {});
    },
    isReady: function() {
      const { creditList, creditTotal } = this;
      return creditList && creditTotal ? true : false;
    },
    isHaveCredit() {
      return this.creditTotal > 0;
    },
    paginationProps() {
      return {
        totalItems: this.creditTotal,
        showEntries: 10,
      };
    },
    creditList() {
      return get(this, "customerCredit.creditList");
    },
    creditTotal() {
      return get(this, "customerCredit.totalCount", 0);
    },
  },
  methods: {
    ...mapActions(["toggleLoading", "notification"]),
    async getCreditList(skip, limit) {
      const { toggleLoading, fetchingCredit } = this;
      toggleLoading({
        loadingKey: fetchingCredit,
        isLoading: true,
      });

      const data = await getCustomerCreditList({ skip, limit });
      const totalCount = get(data, "total_count.0", 0);
      const creditList = Object.values(get(data, "records.0", [])).reverse();
      this.customerCredit = {
        creditList,
        totalCount,
      };

      toggleLoading({
        loadingKey: fetchingCredit,
        isLoading: false,
      });
    },
    async submitCode() {
      try {
        const data = await refillRechargeCode(this.rechargeCode);
        this.errorMsg =
          data.status === "error"
            ? get(data, "data.errors.0", data.message)
            : "";
      } catch (error) {
        this.errorMsg = get(
          error,
          "response.data.messages.error.0.message",
          error.message
        );
      }
      this.rechargeCode = "";
    },
  },
  async mounted() {
    await this.getCreditList();
  },
};
</script>

